/*        */
.excel-filter-row {
  background: linear-gradient(180deg, #eef9fb 0%, #e4f4f7 100%) !important;
  border-bottom: 1px solid #9dc5cd !important;
}
.excel-filter-cell {
  padding: 4px 6px !important;
